<template>
  <ToolWrapper
    v-model="is_edit"
    :class="[class_name, 'ac-esign-tool-text']"
    :is_admin="is_admin"
    :editable.sync="is_editable"
    :has_wrapper="has_wrapper"
    :resize="is_resize"
    :resize_props="resize_props"
    ref="wrapper"
    @enter="update_input_value"
    @remove="remove_inner"
    @update:editable="edit_text"
    @resize_end="resize_end"
    @copy_tool="copyTool"
  >
    <b-sidebar
      :visible="is_editable && is_edit"
      backdrop-variant="transparent"
      backdrop
      @hidden="on_hide_sidebar('close_side_bar')"
      @change="on_hide_sidebar('close_side_bar')"
      title="Edit Text"
      no-header
      right
      shadow
      sidebar-class="top-5 bg-lighter"
      class="bg-lighter esign-sidbar"
    >
      <header class="esign-sidbar-header border-bottom d-flex align-items-center justify-content-between pb-3">
        <div class="headtext">
          <strong>{{ set_title }} Text</strong>
        </div>
        <button type="button" aria-label="Close" class="close text-dark" @click="on_hide_sidebar('close_side_bar')">
          <svg viewBox="0 0 16 16" width="1em" height="1em" focusable="false" role="img" aria-label="x" xmlns="http://www.w3.org/2000/svg" fill="currentColor" class="bi-x b-icon bi">
            <g>
              <path fill-rule="evenodd" d="M11.854 4.146a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708-.708l7-7a.5.5 0 0 1 .708 0z"></path>
              <path fill-rule="evenodd" d="M4.146 4.146a.5.5 0 0 0 0 .708l7 7a.5.5 0 0 0 .708-.708l-7-7a.5.5 0 0 0-.708 0z"></path>
            </g>
          </svg>
        </button>
      </header>
      <div class="esign-sidbar-body mb-3">
        <div class="px-2 py-3 mt-2">
          <span><strong>Note:</strong> <span>Text provided is Auto-saved</span></span>
          <div class="editor-wrapper mt-3">
            <div id="editor" ref="editor" v-html="value"></div>
          </div>
        </div>
      </div>
    </b-sidebar>
    <div class="max-text-filed w-100" ref="value_container" v-html="input_value"></div>
  </ToolWrapper>
</template>

<script>
  import { Quill } from "vue-quill-editor";
  import ToolWrapper from "./AcEsignToolWrapper.vue";
  import { set_focus_to, remove_element, spread_child_nodes } from "../utils";
  import fonts, { FONT_VARIANTS } from "../../../../../public/sign/fonts";
  import "quill/dist/quill.snow.css";
  import "../../../../../public/sign/fonts/fonts.css";
  import CopyMixin from "../copy-tool";

  const fonts_size = [
    { text: 9, value: "9pt" },
    { text: 10, value: "10pt" },
    { text: 11, value: "11pt" },
    { text: 12, value: "12pt" },
    { text: 14, value: "14pt" },
    { text: 16, value: "16pt" },
    { text: 18, value: "18pt" },
    { text: 20, value: "20pt" },
    { text: 22, value: "22pt" },
    { text: 24, value: "24pt" },
    { text: 26, value: "26pt" },
    { text: 28, value: "28pt" },
    { text: 36, value: "36pt" },
  ];

  /**
   * Ac Esign Tool Text - tool of input text
   *
   * @requires ToolWrapper
   */
  export default {
    name: "AcEsignToolText",

    mixins: [CopyMixin],

    components: {
      ToolWrapper,
    },

    props: {
      class_name: [String, Array, Object],

      value: String,

      editable: {
        type: Boolean,
        default: false,
      },

      is_admin: {
        type: Boolean,
        default: false,
      },

      has_wrapper: {
        type: Boolean,
        default: true,
      },
      parent_ref: Object,
      unique_id: String,

      width: {
        type: Number,
        default: 300,
      },

      // Signature pad height
      height: {
        type: Number,
        default: 200,
      },
    },

    directives: {
      autofocus: {
        inserted(el) {
          set_focus_to(el);
        },

        update(el) {
          set_focus_to(el);
        },
      },
    },

    computed: {
      fonts_list() {
        return Object.keys(fonts);
      },

      fonstSizes() {
        return fonts_size;
      },
    },

    destroy() {
      this.remove_fonts();
    },

    watch: {
      is_edit(old, new_val) {
        if (!new_val) {
          this.$nextTick(() => {
            this.set_fonts();
            this.set_quill_editor();
          });
        } else this.on_hide_sidebar("close_side_bar");
        this.set_title = !this.input_value ? "Add" : "Edit";
      },
    },

    data() {
      return {
        // State of focus of current element
        is_edit: false,
        is_resize: true,
        clear_tool_sizes: {},
        close_side_bar: null,
        // Value of current input
        input_value: this.value || "",
        set_title: "Add",
        is_editable: false,

        editor_options: {
          modules: {
            history: {
              delay: 2000,
              maxStack: 500,
              userOnly: true,
            },
            toolbar: [
              [{ font: Object.keys(fonts) }], // custom dropdown
              [{ size: fonts_size.map((item) => item.value) }, "bold", "italic", { color: [] }, { align: [] }, "clean"],
            ],
          },
        },

        popover: false,

        quill: null,

        style_el: null,

        update_process: false,

        hidden: true,
        resize_props: {
          width: this.width,
          height: this.height,
        },
      };
    },

    mounted() {
      this.clear_tool_sizes = { ...this.resize_props };
      this.$emit("clear_tool_sizes", this.clear_tool_sizes);
      this.is_editable = this.editable;
      this.$el.addEventListener("dragstart", () => {
        this.stop_edit();
      });
    },

    methods: {
      async get_value() {
        return {
          type: "html",
          value: this.$refs.value_container,
        };
      },

      get_html() {
        return this.input_value;
      },

      remove_fonts() {
        remove_element(this.style_el);
      },

      on_hide_sidebar(value) {
        this.update_input_value(value);
      },

      /**
       * Unset dragging for current tool when tool is mounted
       * After saving signature tool is available to dragging
       */
      unset_draggable() {
        this.$el.removeAttribute("draggable");
      },

      set_draggable() {
        this.$el.setAttribute("draggable", true);
      },

      set_fonts() {
        if (document.getElementById("ac_esign_tool_text_fonts")) return;
        this.style_el = document.createElement("style");
        this.style_el.setAttribute("id", "ac_esign_tool_text_fonts");
        let css = "";
        this.fonts_list.forEach((font) => {
          css += Object.keys(FONT_VARIANTS)
            .map(
              (v) => `
            .ql-font-${font + (v !== "Regular" ? `-${v}` : "")} {
                font-family: "${font + (v !== "Regular" ? `-${v}` : "")}";
                font-weight: ${/(Bold)/.test(v) ? "bold" : "normal"};
                font-style: ${/(Italic)/.test(v) ? "italic" : "normal"};
            }`,
            )
            .join("");
        });
        document.head.appendChild(this.style_el);

        this.style_el.type = "text/css";
        if (this.style_el.styleSheet) {
          this.style_el.styleSheet.cssText = css;
        } else {
          this.style_el.appendChild(document.createTextNode(css));
        }
      },

      set_quill_editor() {
        const Size = Quill.import("attributors/style/size");
        Size.whitelist = fonts_size.map((item) => item.value);
        Quill.register(Size, true);

        const Align = Quill.import("attributors/style/align");
        Quill.register(Align, true);
        const Font = Quill.import("formats/font");
        Font.whitelist = this.fonts_list.reduce((acc, font) => {
          acc.push(...Object.keys(FONT_VARIANTS).map((v) => (v === "Regular" ? font : `${font}-${v}`)));
          return acc;
        }, []);
        Quill.register(Font, true);

        this.quill = new Quill(this.$refs.editor, {
          ...this.editor_options,
          placeholder: "Click and type...",
          theme: "snow", // or 'bubble'
        });

        this.quill.on("selection-change", this.quill_update_value);
        this.quill.on("text-change", this.quill_text_change);

        const toolbar = this.quill.getModule("toolbar");

        toolbar.addHandler("bold", this.quill_set_bold);
        toolbar.addHandler("italic", this.quill_set_itelic);
        toolbar.addHandler("clean", this.quill_set_clean);
        let html = !this.input_value ? "" : this.input_value;
        let converted_html = this.quill.clipboard.convert(html);
        // Quill js method to show html content as normal text in text field
        this.quill.setContents(converted_html, "html");
        this.quill.focus();
        this.quill.setSelection(this.quill.getLength());
      },

      isQuillEmpty(quill) {
        if ((quill.getContents().ops || []).length !== 1) {
          return false;
        }
        return quill.getText().trim().length === 0;
      },

      update_input_value(stop_edit = false) {
        const editor = this.quill?.container?.querySelector(".ql-editor");
        if (!editor) return;
        const cloned_el = editor.cloneNode(true);
        spread_child_nodes(cloned_el).forEach((node) => {
          if (node.nodeName === "#text") {
            const el = document.createElement("span");
            el.innerHTML = node.textContent;
            node.parentNode.replaceChild(el, node);
          }
        });

        this.input_value = cloned_el.innerHTML;

        if (this.isQuillEmpty(this.quill)) {
          this.input_value = "";
        }
        if (stop_edit) {
          this.stop_edit(stop_edit);
          const val = !this.input_value ? "" : this.input_value;
          this.save_tool_value(val);
        }
      },

      save_tool_value(val) {
        const tools = [...this.parent_ref.tools];
        this.parent_ref.tools = tools.map((tool) => {
          if (tool.unique_id === this.unique_id) tool.props.value = val;

          return tool;
        });
        this.$emit("remove_border", val);
      },

      resize_end(data) {
        const tools = [...this.parent_ref.tools];
        this.parent_ref.tools = tools.map((tool) => {
          if (tool.unique_id === this.unique_id) {
            tool.props.width = data.width;
            tool.props.height = data.height;
          }
          return tool;
        });
      },

      remove_inner() {
        if (this.is_editable) {
          this.input_value = "";
          // Need to clear the quill editor.
          this.quill.setContents([{ insert: "\n" }]);
        }
        this.set_title = !this.input_value ? "Add" : "Edit";
      },

      quill_set_bold(state) {
        const { index, length } = this.quill.getSelection();
        const format = this.quill.getFormat();

        let font = (format.font && format.font.split("-")[0]) || this.fonts_list[0];

        if (Array.isArray(format.font)) {
          [font] = format.font;
        }

        if (state) {
          font = `${font}-${format.italic ? FONT_VARIANTS.BoldItalic : FONT_VARIANTS.Bold}`;
          this.quill.formatText(index, length, { font, bold: true });
        } else {
          font = format.italic ? `${font}-${FONT_VARIANTS.Italic}` : font;
          this.quill.formatText(index, length, { font, bold: false });
        }
      },

      quill_set_itelic(state) {
        const { index, length } = this.quill.getSelection();
        const format = this.quill.getFormat();
        let font = (format.font && format.font.split("-")[0]) || this.fonts_list[0];

        if (Array.isArray(format.font)) {
          [font] = format.font;
        }

        if (state) {
          font = `${font}-${format.bold ? FONT_VARIANTS.BoldItalic : FONT_VARIANTS.Italic}`;
          this.quill.formatText(index, length, { font, italic: true });
        } else {
          font = format.bold ? `${font}-${FONT_VARIANTS.Bold}` : font;
          this.quill.formatText(index, length, { font, italic: false });
        }
      },

      // Added clean functionality for quill editor
      quill_set_clean() {
        this.quill.deleteText(0, this.quill.getLength());
        this.quill.history.clear();
      },

      quill_update_value(range, oldRange) {
        if (range === null && oldRange !== null) {
          this.update_input_value(false);
          // Clicked outside the quill editor
        }
      },

      quill_text_change() {
        if (this.update_process) return;

        this.update_process = true;
        this.update_input_value(false);

        setTimeout(() => {
          this.update_input_value(false);
          this.update_process = false;
        }, 300);
      },

      /**
       * Preparing props for reusable
       */
      get_props() {
        const tools = [...this.parent_ref.tools];
        const component = tools.filter((tool) => {
          if (tool.unique_id === this.unique_id) {
            return tool;
          }
        })[0];
        return { ...component.props };
      },

      stop_edit(value) {
        if (this.close_side_bar) return;
        if (value == "close_side_bar") {
          this.close_side_bar = value;
          if (!this.input_value) this.$emit("reset");
          this.is_edit = false;
        } else this.is_edit = true;
      },

      edit_text($event) {
        this.set_title = !this.input_value ? "Add" : "Edit";
        if ($event) {
          this.is_edit = $event;
          this.set_fonts();
          this.$nextTick(() => {
            this.set_quill_editor();
          });
        } else {
          this.is_edit = $event;
          this.is_editable = !$event;
        }
      },

      /**
       * Triggered by on keyup delete or backspace
       * @param {KeyboardEvent} event
       */
      on_delete(event) {
        if (event.key === "Backspace") {
          event.stopPropagation();
        }
      },
    },
  };
</script>
